import { UploadService } from './interfaces/UploadService';
import { ProvinceModel, CategoryModel, BrandModel, ModelModel, NewsModel } from '@app/machine-news';
import { UserInputError, NotAuthorizedError } from '@app/core';
import { UsersModel } from '@app/auth';
import admin from 'firebase-admin';
import slugify from 'slugify';
import { convertVietnamese } from '@client/core';
import { readFile, utils } from 'xlsx';
import replace from 'lodash/replace';

export const uploadService: UploadService = {
  createNewsFromExcelRow: async (row, index, newsType) => {
    const newsStates = [
      { value: 'NEW', label: 'Mới' },
      { value: 'USED', label: 'Nhập bãi' },
      { value: 'LIQUIDATE', label: 'Thanh lý' },
    ];

    const phoneNo = `+${row['A']}`.trim();
    const firstName = row['B'].trim();
    const newsState = newsStates.filter((state) => state.label === row['C'])[0] ? newsStates.filter((state) => state.label === row['C'])[0].value : '';
    const location = row['D'] ? await ProvinceModel.findOne({slug: slugify(convertVietnamese(row['D'].trim()))}).exec() : '';
    const usedHours = row['E'];
    const price = row['F'];
    const serialNo = row['G'];
    const category: any = await CategoryModel.findOne({slug: slugify(convertVietnamese(row['H'].trim()))}).exec();
    const brand: any = row['I'] ? await BrandModel.findOne({slug: slugify(convertVietnamese(row['I'].trim()))}).exec() : '';
    const model: any = row['J'] ? await ModelModel.findOne({slug: row['J'].trim()})
      .populate('brand')
      .populate('category')
      .exec() : '';
    const description = row['K'];
    const year = row['L'];
    const rentalPeriod = row['M'].split('/')[1];
    const weightRange = row['N'].split('/')[1];
    const imageUrls = row['O'] ? row['O'].split('\n') : [];

    // validate
    if (!phoneNo || !firstName) {
      throw new UserInputError(`Invalid user info at row ${index}`);
    }
    let existedUser = await UsersModel.findOne({phoneNo}).exec();
    if (!existedUser) {
      // create firebase user
      const newUser = await admin.auth().createUser({
        phoneNumber: phoneNo,
        password: 'Timmay@123',
        email: `${phoneNo.replace('+', '')}@timmay.vn`,
        displayName: firstName,
        emailVerified: true,
      });

      // create mongo user
      existedUser = await UsersModel.create({
        id: newUser.uid,
        email: newUser.email,
        phoneNo,
        familyName: firstName,
        givenName: '',
        fullName: firstName,
        loginDetail: {
          phoneNo: newUser.phoneNumber,
          provider: 'phone',
        },
        roles: [],
        isActive: true,
        completeSignUp: false,
        createdBy: newUser.uid,
        createdAt: new Date().getTime(),
      });
    }

    if (['SELL', 'LEASE'].indexOf(newsType) > -1 && !newsState) {
      throw new UserInputError(`Invalid news state at row ${index}`);
    }

    if (row['D'] && (!location || !location._id)) {
      throw new UserInputError(`Invalid location at row ${index}`);
    }

    if (usedHours && isNaN(usedHours)) {
      throw new UserInputError(`Invalid used hours at row ${index}`);
    }

    if (price && isNaN(price)) {
      throw new UserInputError(`Invalid price at row ${index}`);
    }

    if (!category || !category._id) {
      throw new UserInputError(`Invalid category at row ${index}`);
    }

    if (['SELL', 'LEASE'].indexOf(newsType) > -1 && (!brand || !brand._id)) {
      throw new UserInputError(`Invalid brand at row ${index}`);
    }

    if (['SELL', 'LEASE'].indexOf(newsType) > -1 && (!model || !model._id)) {
      throw new UserInputError(`Invalid model at row ${index}`);
    }

    if (newsType === 'RENT' && (!rentalPeriod)) {
      throw new UserInputError(`Invalid rental period at row ${index}`);
    }

    if (!weightRange) {
      throw new UserInputError(`Invalid weight range at row ${index}`);
    }

    if (['SELL', 'LEASE'].indexOf(newsType) > -1 && imageUrls.length === 0) {
      throw new UserInputError(`Missing images at row ${index}`);
    }

    let title = '';
    switch (newsType) {
      case 'SELL':
        title = `${year ? year : ''} ${model.brand.name.toUpperCase()} ${model.name} ${serialNo}`;
        break;
      case 'LEASE':
        title = `${year ? year : ''} ${model.brand.name.toUpperCase()} ${model.name} ${serialNo}`;
        break;
      case 'BUY':
        title = `Tìm mua ${category.name}`;
        break;
      case 'RENT':
        title = `Tìm thuê ${category.name}`;
        break;
      default:
        break;
    }

    // create news
    const news = new NewsModel({
      title,
      state: newsState,
      newsType,
      location: location ? location._id : undefined,
      owner: existedUser._id,
      status: 'PUBLIC',
      description,
      usedHours,
      price,
      serialNo,
      categoryId: model && model.category ? model.category._id : category ? category._id : undefined,
      brand: model && model.brand ? model.brand._id : brand ? brand._id : undefined,
      model: model ? model._id : undefined,
      year,
      weightRange,
      rentalPeriod,
      createdAt: new Date().getTime(),
      imageUrls,
    });
    await news.save();
    await news.populate('categoryId').populate('weightRange').execPopulate();
    return news;
  },
  uploadExcelFile: async (file: any, authorizationHeader: string) => {
    const idToken = replace(authorizationHeader, 'Bearer ', '');
    const decodedIdToken = await admin.auth().verifyIdToken(idToken);
    if (decodedIdToken.roles.indexOf('5c7f747708898183ac62f2af') > -1) {
      const newsTypes = ['SELL', 'LEASE', 'BUY', 'RENT'];
      const workbook = readFile(`upload/${file.filename}`);
      const createNewsPromises = [];

      for (let i = 2; i < workbook.SheetNames.length; i += 1) {
        const newsType = newsTypes[i - 2];
        const wordSheet = workbook.Sheets[workbook.SheetNames[i]];
        const sheetData = utils.sheet_to_json(wordSheet, {header: 'A'});

        for (let j = 2; j < sheetData.length; j += 1) {
          createNewsPromises.push(uploadService.createNewsFromExcelRow(sheetData[j], j, newsType));
        }
      }

      const data = await Promise.all(createNewsPromises);
      return {
        data,
      };
    } else {
      throw new NotAuthorizedError();
    }
  },
};
